        type="text"
        @click:append-outer="sendMessage"
        @click:clear="clearMessage"
      ></v-text-field>
    </v-footer>
  </v-sheet>
</template>

<script>
export default {
  data: () => ({
    password: "Password",
    show: false,
    message: "Hey!",
    messages: [
      {
        text: "12/30 北京長城一日遊，請準備飲用水和午餐。謝謝。",
        self: true,
      },
      {
        text: "請問可以陪同嗎?",
      },
      {
        text: "可以",
        self: true,
      },
      {
        text: "今天遠足很開心，謝謝老師",
      },
      {
        text: "不客氣",
        self: true,
      },
    ],
  }),

  computed: {
    icon() {
      return this.icons[this.iconIndex];
    },
  },

  methods: {
    sendMessage() {
      if (!this.message) return;
      this.messages.push({
        self: true,
        text: this.message,
      });
      this.clearMessage();
    },
    clearMessage() {
      this.message = "";
    },
  },
};
</script>

